import router from './router.js';
import store from '../store';

// 
const whiteList = ['home', 'preview', 'fileshare', 'ssh', 'javadoc', 'collected', 'login', 'loginx', 'register', 'docfocus'];

router.beforeEach((to, from, next) => {
    console.log('store.state.user.token: ' + store.state.user.token);
    console.log('to.name: ' + to.name);

    // token
    if (store.state.user.token !== undefined && store.state.user.token !== '') {
        // 
        if (to.name === 'login') {
            next({ path: '/' });
        } else {
            next();
        }
    } else {
        if (whiteList.indexOf(to.name) !== -1) {
            // 
            next();
        } else {
            // 
            next(`/login?redirect=${to.path}`);
        }
    }
});

export default router;
