// warterMark.js
'use strict';

let watermark = {};

let setWatermark = (str, str1) => {
    let id = '1.23452384164.123412415';

    if (document.getElementById(id) !== null) {
        document.body.removeChild(document.getElementById(id));
    }

    let can = document.createElement('canvas'); // 
    can.width = 350; // 
    can.height = 300; // 

    let cans = can.getContext('2d');
    cans.rotate((-15 * Math.PI) / 180); //     0 
    cans.font = '14px Vedana'; // 
    cans.fillStyle = '#222222'; //  F7F7F7 919191
    cans.textAlign = 'left'; // 
    cans.textBaseline = 'Middle'; // 
    cans.globalAlpha = 0.1; // 
    cans.fillText(str, can.width / 3, can.height / 2); // XY
    cans.fillText(str1, can.width / 3, can.height / 2.5); // str1

    let div = document.createElement('div');
    div.id = id;
    div.style.pointerEvents = 'none';
    div.style.top = '70px';
    div.style.left = '0px';
    div.style.position = 'fixed';
    div.style.zIndex = '100000';
    div.style.width = document.documentElement.clientWidth - 30 + 'px';
    div.style.height = document.documentElement.clientHeight + 'px';
    div.style.background = 'url(' + can.toDataURL('image/png') + ') left top repeat';
    document.body.appendChild(div);
    return id;
};

// 
watermark.set = (str, str1) => {
    let id = setWatermark(str, str1);
    setInterval(() => {
        if (document.getElementById(id) === null) {
            id = setWatermark(str, str1);
        }
    }, 500);
    window.onresize = () => {
        setWatermark(str, str1);
    };
};

export default watermark;
